#card{width:100%;height:100%;-webkit-transform-style:preserve-3d;transform-style:preserve-3d;-webkit-transition:-webkit-transform 0.5s;transition:-webkit-transform 0.5s;-o-transition:transform 0.5s;transition:transform 0.5s;transition:transform 0.5s, -webkit-transform 0.5s;-webkit-transition-timing-function:ease-in-out;-o-transition-timing-function:ease-in-out;transition-timing-function:ease-in-out;
    -webkit-backface-visibility: visible;
    -webkit-backface-visibility: hidden;}
.back{-webkit-transform:rotateY(180deg);transform:rotateY(180deg);z-index:1}
.back,.front{position:absolute;top:0;left:0;-webkit-backface-visibility:hidden;backface-visibility:hidden}
.container{-webkit-perspective:400px;perspective:400px;position:relative;z-index:1}



